//解析url参数
//example ?id=12345&a=b
//return Object {id:12345,a:b}
function urlParse() {
  var query = window.location.search;
  var obj = {};
  var reg = /[?&][^?&]+=[^?&]+/g;
  var arr = query.match(reg);
  if (arr) {
    arr.forEach(function (item) {
      var tempArr = item.substr(1).split('=');
      var key = tempArr[0];
      var value = tempArr[1];
      obj[key] = decodeURIComponent(value);
    });
  }
  // console.log(obj)
  return obj;
}

